import uniList from '@/components/uni/uni-list/uni-list.vue'
import uniListItem from '@/components/uni/uni-list-item/uni-list-item.vue'
import uniPopup from "@/components/uni/uni-popup/uni-popup.vue"
export default {
	components: {
		uniList,
		uniListItem,
		uniPopup
	},
	data() {
		return {
			show: false,
			courier_name:'',
			courier: '',
			courier_num: '',
			id: '',
			name_list: [{ 
					name:'顺丰速运',
					value:'SF',
				},
				{ 
					name:'百世快递',
					value:'HTKY',
				},
				{ 
					name:'中通快递',
					value:'ZTO',
				},
				{ 
					name:'申通快递',
					value:'STO',
				},
				{ 
					name:'圆通速递',
					value:'YTO',
				},
				{ 
					name:'韵达速递',
					value:'YD',
				},
				{ 
					name:'邮政快递包裹',
					value:'YZPY',
				},
				{ 
					name:'EMS',
					value:'EMS',
				},
				{ 
					name:'天天快递',
					value:'HHTT',
				},
				{ 
					name:'京东快递',
					value:'JD',
				},
				{ 
					name:'优速快递',
					value:'UC',
				},
				{ 
					name:'德邦快递',
					value:'DBL',
				},
				{ 
					name:'宅急送',
					value:'ZJS',
				},
				{
					name:'UPS',
					value:'UPS',
				},
				{
					name:'其他',
					value:'0',
				}
			]
		}
	},
	onLoad(options) {
		this.id = options.id
	},
	methods: {
		sub_send() {
			if(!this.courier_num){
				this.$api.msg('快递单号不能为空')
				return;
			}
			this.$api.http.post('order/mcms/edit_courier',{
				courier:this.courier,
				courier_num:this.courier_num,
				order_id:this.id
			}).then(res=>{
				if(res.status == 400){
					this.$api.msg(res.msg)
					setTimeout(()=>{uni.navigateBack({})},2000)
				}
				if(res.status == 200){
					this.$api.msg('发货成功')
					setTimeout(()=>{uni.navigateBack({})},2000)
				}
				
			})
		},
		choose_name(e){
			console.log(e.detail)
			const index = e.detail.value
			this.courier = this.name_list[index].value
			this.courier_name = this.name_list[index].name
			console.log(this.courier)
			this.show = false
			
		},
	}
}
